<!-- 添加login路由 -->
<template>
  <div class="boxed_wrapper">
    <!-- <div class="mask"></div> -->

    <div class="login_dialog" v-if="login_dialog">
      <div class="login_views">
        <span
          class="passwork_login"
          @click="flag = '密码登录'"
          :class="[flag == '密码登录' ? 'bottom_line' : '']"
          >密码登录</span
        >
        <span
          class="unpasswork_login"
          v-if="flag == '找回密码'"
          :class="[flag == '找回密码' ? 'bottom_line' : '']"
          >找回密码</span
        >
        <div
          class="passwork_login_view"
          v-if="flag == '密码登录' ? true : false"
        >
          <input
            type="text"
            placeholder="账号"
            v-model="username"
            @keyup.enter="idEnter()"
          />
          <input
            type="password"
            placeholder="密码"
            v-model="password"
            class="passwordInupt"
            @keyup.enter="submit_login"
          />
          <a href="javascript:;" class="submit_passwork" @click="submit_login"
            >登录</a
          >
        </div>
      </div>
    </div>
    <div class="sysUpdateNotify" v-if="!login_dialog">
      <h2>
        <i class="el-icon-message"></i>系统近一周更新
        <span class="toHistory" @click="toUpdatePage">历史记录</span>
      </h2>
      <div class="notifyContent">
        <ul>
          <li v-for="(date, index) in listArr" :key="index">
            {{ listdata[index].time }}
            <ul class="updateLiDetail">
              <li v-for="(content, idx) in listdata[index].value" :key="idx">
                {{ content }}
              </li>
            </ul>
          </li>
          <li v-if="!listArr.length">暂无数据</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      listdata: [],
      listArr: [],
      initData: [],
      flag: "密码登录",
      username: "", //用户名
      password: "", //密码
      login_state: "", // 原来为登陆，登录成功后显示已登录
      login_dialog: true
    };
  },
  computed: {
    stateYear() {
      return this.$store.state.status;
    }
  },
  watch: {
    stateYear: {
      handler(val) {
        this.$nextTick(() => {
          this.login_state = val;
        });
      }
    },
    immediate: true
  },
  mounted() {
    this.if_login();
  },
  methods: {
    toUpdatePage() {
      this.$router.push({
        path: "/SysUpdateLog"
      });
    },
    submit_login() {
      let that = this;
      let formData = new FormData();
      formData.append("user", this.username);
      formData.append("pwd", this.password);
      console.log(this.username);
      console.log(this.password);
      console.log(formData);
      this.$axios
        .post(that._url2 + "server/personnel_management/login/", formData)
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            console.log(res.data);
            let token = res.data.token;
            // document.cookie = "token=" + token;
            // document.cookie = "area=" + res.data.area;
            // document.cookie = "user_name=" + res.data.user_name;
            sessionStorage.setItem("token", token);
            sessionStorage.setItem("area", res.data.area);
            sessionStorage.setItem("user_name", res.data.user_name);
            sessionStorage.setItem(
              "PERMISSION_SESSION_KEY",
              res.data.PERMISSION_SESSION_KEY
            );
            that.login_state = "已登录";
            that.$store.state.status = "已登录";
            that.login_dialog = false;
            // 请求系统更新记录
            this.getLastWeekData();
          } else {
            alert("密码或用户名错误");
            that.login_state = "登录";
            that.$store.state.status = "登录";
          }
        });
    },
    getLastWeekData() {
      let url =
        this._url2 + "server/personnel_management/select_system_update_log/";
      this.$axios
        .get(url, {
          params: {
            update_area: "",
            data_date: ""
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.initData = res.data.data || [];
            let date = new Date();
            let duration = 3600 * 1000 * 24 * 7;
            let deadLine = date.getTime() - duration;
            console.log(deadLine, new Date(deadLine));
            this.initData.forEach((item, i) => {
              console.log(
                new Date(item.update_time),
                new Date(item.update_time).getTime()
              );
              let updateTime = new Date(item.update_time).getTime();
              if (updateTime < deadLine) {
                console.log(updateTime, deadLine, "不在一周内");
              } else {
                console.log(updateTime, deadLine, "在一周内");
                let idx = this.listArr.indexOf(item.update_time);
                if (idx > -1) {
                  // 有了
                  this.listdata[idx].value.push(item.update_info);
                } else {
                  // 没有
                  this.listArr.push(item.update_time);
                  let obj = {};
                  obj["time"] = item.update_time;
                  obj["value"] = [];
                  obj["value"].push(item.update_info);
                  this.listdata.push(obj);
                }
              }
            });

            this.listdata = this.listdata.sort((a, b) => {
              console.log(
                new Date(b.time).getTime(),
                new Date(a.time).getTime()
              );
              return new Date(b.time).getTime() - new Date(a.time).getTime();
            });
            console.log(this.listdata, this.listArr);
          }
        });
    },
    if_login() {
      if (this.$store.state.status == "已登录") {
        this.login_dialog = false;
        // 请求系统更新记录
        this.getLastWeekData();
      } else {
        this.login_dialog = true;
      }
    },
    idEnter() {
      document.querySelector(".passwordInupt").focus();
    }
  }
};
</script>

<style scoped>
.toHistory {
  font-size: 12px;
  color: blue;
  text-decoration: underline;
  cursor: pointer;
  margin-left: 10px;
  vertical-align: 2px;
}
.sysUpdateNotify {
  position: absolute;
  width: 300px;
  /* height: 400px; */

  background: rgba(255, 255, 255, 0.7);
  right: 25px;
  margin-top: 25px;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.1);
  height: 20px;
}
.sysUpdateNotify .notifyContent {
  padding: 0 20px;
  font-size: 14px;
  font-weight: bold;
  max-height: 330px;
  overflow-y: auto;
  margin-bottom: 20px;
}
.notifyContent .updateLiDetail {
  margin-left: 30px;
  font-weight: normal;
}
.updateLiDetail li {
  list-style-type: disc;
}
.sysUpdateNotify h2 {
  font-size: 16px;
  text-align: center;
  line-height: 50px;
}
.sysUpdateNotify .el-icon-message {
  vertical-align: -2px;
  margin-right: 7px;
  font-size: 20px;
}
.login_dialog {
  /* display: none; */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 360px;
  /* height: 340px; */
  min-height: 340px;
  border-radius: 10px;
  border: 1px solid #dddddd;
  padding: 30px;
  text-align: left;
  background-color: white;
  animation: logima 1s;
  z-index: 2;
}

.passwork_login {
  display: inline-block;
  padding: 10px 0px 10px 0;
  margin-right: 30px;
  font-size: 18px;
  cursor: pointer;
}

.unpasswork_login {
  display: inline-block;
  padding: 10px 0px 10px 0;
  margin-right: 30px;
  font-size: 18px;
  cursor: pointer;
}

.login_dialog input:nth-of-type(1) {
  margin-top: 40px;
}

.login_dialog input {
  margin-top: 20px;
  width: 290px;
  height: 40px;
  border-left: none;
  border-top: none;
  border-right: none;
  border-bottom: 1px black 1px;
  text-indent: 1em;
  background-color: transparent;
}

.foget_passwork {
  margin-left: 230px;
  padding-top: 10px;
  color: #797979;
  display: inline-block;
}

.foget_passwork:hover {
  color: skyblue;
}

.submit_passwork:hover {
  color: skyblue;
}

.submit_passwork {
  display: inline-block;
  width: 298px;
  height: 50px;
  margin-top: 15px;
  border-radius: 10px;
  background-color: red;
  line-height: 50px;
  text-align: center;
  color: white;
  background: linear-gradient(to bottom right, #fb5056, #f92b74);
  animation: logima2 1s;
}
.submit_new_password {
  padding: 10px 20px;
  /* width: 100px; */
  border: 1px solid #eee;
  border-radius: 10px;
  background: gray;
  margin-top: 15px;
  /* display: inline-block; */
  text-align: center;
}

.unpasswork_login_view {
  display: flex;
  flex-direction: column;
  position: relative;
}

.unpasswork_login_view p {
  position: absolute;
  top: 46px;
  left: 15px;
  border-right: 1px solid #dddddd;
  padding-right: 10px;
}

.unpasswork_login_view input:nth-of-type(1) {
  text-indent: 4.2em;
}

.send_verification {
  position: absolute;
  width: 80px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  background-color: #ededed;
  color: #797979;
  border-radius: 10px;
  font-size: 14px;
}

.bottom_line {
  border-bottom: 5px solid #797979;
}

@keyframes logima {
  0% {
    top: 0%;
    /* left: 73%;  */
    opacity: 0;
    /* width: 150px; */
  }
  100% {
    opacity: 1;
    top: 50%;
    left: 50%;
    width: 360px;
  }
}

@keyframes logima2 {
  0% {
    top: 0%;
    /* left: 73%;  */
    opacity: 0;
    width: 0;
  }
  100% {
    opacity: 1;
    /* top: 50%; */
    /* left: 50%;  */
    width: 298px;
  }
}

.find_password,
.find_password2 {
  display: none;
}
</style>



